import React, { useState } from 'react';
import { format, startOfMonth, endOfMonth, eachDayOfInterval, isSameMonth, isSameDay, startOfWeek, addDays, isWithinInterval } from 'date-fns';
import './CalendarGrid.css';

const CalendarGrid = ({ onDateSelect, initialEvents = [] }) => {
    const [selectedDate, setSelectedDate] = useState(new Date());
    const [currentMonth, setCurrentMonth] = useState(new Date());
    const [events, setEvents] = useState(initialEvents);
    const [startDate, setStartDate] = useState(null);
    const [endDate, setEndDate] = useState(null);

    const monthStart = startOfMonth(currentMonth);
    const monthEnd = endOfMonth(currentMonth);
    const startDateOfWeek = startOfWeek(monthStart, { weekStartsOn: 1 });
    const endDateOfMonth = endOfMonth(monthEnd);
    const daysInMonth = eachDayOfInterval({ start: startDateOfWeek, end: endDateOfMonth });

    const handleDateClick = (day) => {
        setSelectedDate(day);
        if (onDateSelect) {
            onDateSelect(day);
        }

        if (!startDate) {
            setStartDate(day);
            setEndDate(day);
        } else if (!endDate) {
            if (day < startDate) {
                setEndDate(startDate);
                setStartDate(day);
            } else {
                setEndDate(day);
            }
        } else {
            setStartDate(day);
            setEndDate(null);
        }
    };

    const handleAddEvent = () => {
        if (startDate && endDate) {
            const newEvent = {
                id: Date.now(),
                start: startDate,
                end: endDate,
                title: 'Новое событие'
            };
            setEvents([...events, newEvent]);
            setStartDate(null);
            setEndDate(null);
        }
    };

    const handlePrevMonth = () => {
        setCurrentMonth(new Date(currentMonth.setMonth(currentMonth.getMonth() - 1)));
    };

    const handleNextMonth = () => {
        setCurrentMonth(new Date(currentMonth.setMonth(currentMonth.getMonth() + 1)));
    };

    const isDateInRange = (day) => {
        if (!startDate || !endDate) return false;
        return isWithinInterval(day, { start: startDate, end: endDate });
    };

    const isDateInEvent = (day) => {
        return events.some(event => isWithinInterval(day, { start: event.start, end: event.end }));
    };

    return (
        <div className="calendar-container">
            <div className="calendar-header">
                <button onClick={handlePrevMonth}>&lt;</button>
                <h2>{format(currentMonth, 'MMMM yyyy')}</h2>
                <button onClick={handleNextMonth}>&gt;</button>
            </div>
            <div className="calendar-grid">
                <div className="weekday">Пн</div>
                <div className="weekday">Вт</div>
                <div className="weekday">Ср</div>
                <div className="weekday">Чт</div>
                <div className="weekday">Пт</div>
                <div className="weekday">Сб</div>
                <div className="weekday">Вс</div>
                {daysInMonth.map((day, index) => (
                    <div
                        key={index}
                        className={`calendar-day ${
                            !isSameMonth(day, currentMonth) ? 'different-month' : ''
                        } ${isSameDay(day, selectedDate) ? 'selected' : ''} ${
                            isDateInRange(day) ? 'in-range' : ''
                        } ${isDateInEvent(day) ? 'has-event' : ''}`}
                        onClick={() => handleDateClick(day)}
                    >
                        {format(day, 'd')}
                    </div>
                ))}
            </div>
            {startDate && endDate && (
                <button className="add-event-button" onClick={handleAddEvent}>
                    Добавить событие
                </button>
            )}
        </div>
    );
};

export default CalendarGrid;
