import {memo} from "react";
import {Handle, Position, useReactFlow} from "@xyflow/react";
import {Delete, Mail} from "../Icon";
import "./messageNode.css"
import {Header} from "./Header";
import {Action} from "./Action";
import {Body} from "./Body";
import {usePanelContext} from "../../contexts/PanelContext";

export default memo(({id, data, isConnectable, selected}) => {
    const {deleteElements} = useReactFlow()
    const {setVisible} = usePanelContext()

    return (
        <>
            <div style={{width: "100%"}}>
                <Handle
                    type={"target"}
                    position={Position.Left}
                    isConnectable={isConnectable}
                />
                <Header>
                    <div className={"icon"}>
                        <Mail color={"#006600"}/>
                    </div>
                    <span className={"header"}>{data.label}</span>
                    <div className={"addition"}>
                        <Action icon={<Delete color={"#cc0505"}/>} callback={() => {
                            setVisible(false)
                            deleteElements({nodes: [{id}]}).then(r => console.log(r))
                        }}/>
                    </div>
                </Header>
                <div className={'content'}>{data.content}</div>
                <Body>
                    {data.buttons?.map(element => (
                        <>{element.text} {element.callback_data}</>
                    ))}
                </Body>
                <Handle
                    type={"source"}
                    position={Position.Right}
                    isConnectable={isConnectable}
                />
            </div>
        </>
    )
})
