import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import reportWebVitals from './reportWebVitals';
import BotBuilderScreen from "./app/screens/BotBuilderScreen";
import CalendarScreen from "./app/screens/CalendarScreen";
import {CalendarProvider} from "./app/contexts/calendarContext";
import CalendarExample from "./app/components/calendar/CalendarExample";

const taskList = document.getElementsByClassName('task-list-form')
if (taskList.length) {
    for (const element of taskList) {
        const root = ReactDOM.createRoot(element);
        root.render(
            <React.StrictMode>
                <BotBuilderScreen />
            </React.StrictMode>
        )
    }
}

const calendarGrid = document.getElementsByClassName('calendar-container-app')
if (calendarGrid && calendarGrid.length) {
    for (const element of calendarGrid) {
        const root = ReactDOM.createRoot(element);
        root.render(
            <React.StrictMode>
                <CalendarExample />
            </React.StrictMode>
        )
    }
}

const calendar = document.getElementsByClassName('calendar')
if (calendar.length) {
    for (const element of calendar) {
        const root = ReactDOM.createRoot(element);
        root.render(
            <React.StrictMode>
                <CalendarProvider>
                    <CalendarScreen />
                </CalendarProvider>
            </React.StrictMode>
        )
    }
}

reportWebVitals();
