import React, {useCallback, useEffect, useRef} from "react";
import {
    Background,
    ReactFlow,
    useNodesState,
    useEdgesState,
    addEdge,
    ReactFlowProvider,
    useReactFlow,
    Panel,
    useStoreApi,
    useUpdateNodeInternals,
} from '@xyflow/react';
import "./botBuildScreen.css"
import '@xyflow/react/dist/style.css';
import {MessageNode, LockedNode} from "../components/nodes";
import {Add, Remove, Target} from "../components/Icon";
import RemovableEdge from "../components/edges/RemovableEdge";
import AdvancedPanel from "../components/AdvancedPanel";
import LeftPanel from "../components/LeftPanel";
import {DnDProvider, useDnDContext} from "../contexts/DnDContext";
import {PanelContextProvider, usePanelContext} from "../contexts/PanelContext";
import InlineNode from "../components/nodes/InlineNode";

let id = 1;
const getId = () => `${id++}`;
const initialNodes = [
    {
        id: '0',
        type: 'locked',
        selectable: false,
        data: {label: '/start', type: 'vertical', handlers: "input"},
        position: {x: 0, y: 50},
    },
];
const nodeTypes = {
    message: MessageNode,
    locked: LockedNode,
    inline: InlineNode
}
const edgeTypes = {removable: RemovableEdge}

const Flow = () => {
    const reactFlowWrapper = useRef();
    const store = useStoreApi();
    const {screenToFlowPosition, setCenter, zoomIn, zoomOut} = useReactFlow();
    const updateNodeInternals = useUpdateNodeInternals()
    const {setVisible, setCurrent} = usePanelContext()
    const {type} = useDnDContext()
    const {addSelectedNodes} = store.getState()
    const [nodes, setNodes, onNodesChange] = useNodesState(initialNodes);
    const [edges, setEdges, onEdgesChange] = useEdgesState([]);

    const onConnect = useCallback((params) => {
        setEdges((eds) => addEdge({...params, type: 'removable'}, eds))
    }, [screenToFlowPosition]);
    const handleCenter = useCallback(() => {
        const {nodeLookup} = store.getState();
        const nodes = Array.from(nodeLookup).map(([, node]) => node);
        if (nodes.length) {
            const node = nodes[0];
            if (node.measured && node.position) {
                setCenter(node.position.x + node.measured.width / 2, node.position.y + node.measured.height / 2, {
                    zoom: 1,
                    duration: 800
                })
            }
        }
    }, [setCenter])
    const onDragOver = (event) => {
        event.preventDefault()
        event.dataTransfer.dropEffect = "move"
    }
    const onDrop = (event) => {
        event.preventDefault();
        if (!type) {
            return;
        }

        const position = screenToFlowPosition({
            x: event.clientX,
            y: event.clientY,
        });
        const newNode = {
            id: getId(),
            type,
            position,
            selectable: true,
            data: {label: `${type}`, content: 'Message', buttons: []},
            extent: 'parent',
        };

        setNodes((nds) => nds.concat(newNode));
    }
    const onNodeClick = (event, node) => {
        if (node.type !== "locked" && node.type !== "inline") {
            setVisible(true)
            setCurrent(node)
            addSelectedNodes([node.id])
        }
    }
    const onNodeContextMenu = (event, node) => {
        event.preventDefault();
        console.log(node)
    }
    useEffect(() => {
        const {nodeLookup} = store.getState();
        const nodesNested = Array.from(nodeLookup).map(([, node]) => node);
        id = nodesNested.length
        nodesNested.map(element => updateNodeInternals(element.id))
    }, [nodes]);

    const flowProps = {
        style: {backgroundColor: "#F7F9FB"},
        nodes: nodes,
        edges: edges,
        onNodesChange: onNodesChange,
        onEdgesChange: onEdgesChange,
        onConnect: onConnect,
        nodeTypes: nodeTypes,
        edgeTypes: edgeTypes,
        nodeClickDistance: 8,
        snapToGrid: true,
        defaultViewport: {x: 0,y: 0, zoom: 0},
        onDrop: onDrop,
        onDragOver: onDragOver,
        onNodeClick: onNodeClick,
        onNodeContextMenu: onNodeContextMenu
    }

    return (
        <div className="wrapper" ref={reactFlowWrapper}>
            <LeftPanel/>
            <ReactFlow fitView {...flowProps}>
                <Panel style={{marginLeft: "90px"}}>
                    <div className={"form-group"}>
                        <button className={"btn btn-shadow"} onClick={() => zoomIn({duration: 300})}>
                            <Add/>
                        </button>
                        <button className={"btn btn-shadow"} onClick={handleCenter}>
                            <Target/>
                        </button>
                        <button className={"btn btn-shadow"} onClick={() => zoomOut({duration: 300})}>
                            <Remove/>
                        </button>
                    </div>
                </Panel>
                <Background/>
            </ReactFlow>
            <AdvancedPanel onChangeButton={(value) => setNodes([...nodes, value])} />
        </div>
    );
};

export default function BotBuilderScreen() {
    return (
        <ReactFlowProvider>
            <DnDProvider>
                <PanelContextProvider>
                    <Flow/>
                </PanelContextProvider>
            </DnDProvider>
        </ReactFlowProvider>
    )
}