import React, {useEffect, useState} from "react";
import "./advancedPanel.css"
import {usePanelContext} from "../contexts/PanelContext";

const AdvancedPanel = ({onChangeButton}) => {
    const [message, setMessage] = useState("")
    const [buttons, setButtons] = useState([])
    const {current, setCurrent, setVisible, visible} = usePanelContext()
    let nextId = 0;

    const onClickHandler = () => {
        onChangeButton({
            id: `button-${current.id}-${nextId}`,
            type: 'inline',
            position: {
                x: 0,
                y: 0,
            },
            parentNode: current.id,
        })
        setButtons([...buttons, {
            text: "",
            callback_data: ""
        }])
    }
    const onChangeInlineText = (event, button) => {
        const nextButtons = buttons.map((element) => element.id === button.id ? {
            ...element, text: event.currentTarget.value
        } : element)
        setButtons(nextButtons)
    }
    const onChangeInlineCallback = (event, button) => {
        const nextButtons = buttons.map((element) => element.id === button.id ? {
            ...element, callback_data: event.currentTarget.value
        } : element)
        setButtons(nextButtons)
    }
    useEffect(() => {
        if (current) {
            setMessage(current.data.content)
            setButtons(current.data.buttons)
        }
    }, [current]);
    useEffect(() => {
        console.log({
            ...current,
            data: {
                content: message,
                buttons: buttons
            }
        })
        setCurrent({
            ...current,
            data: {
                content: message,
                buttons: buttons
            }
        })
    }, [message, buttons]);

    return (
        <aside className={["advanced-panel", visible ? "show" : "hidden"].join(" ")}>
            <header className={"header"}>
                <span className={"h3"}>Advanced menu</span>
                <span className={"btn close"} onClick={_ => {
                    setVisible(false)
                }}>&times;</span>
            </header>
            <article className={"content"}>
                <div className={"form-input"} key={`panel-message`}>
                    <label className={"form-label"} htmlFor={"message"}>Message</label>
                    <textarea className={"form-control"} id={"message"} rows={3} value={message} onChange={event => {
                        setMessage(event.currentTarget.value)
                    }}/>
                </div>
                <div className={"form-input"} key={`panel-submit`}>
                    <span className={"btn btn-success"} onClick={onClickHandler}>
                        Add button
                    </span>
                </div>
                {buttons && buttons.map((element, index) => (
                    <div key={`child-${index}`}>
                        <div className={"form-input"} key={`child-${index}-text`}>
                            <label className={"form-label"}>Text</label>
                            <input className={"form-control"} onChange={_ => onChangeInlineText(_, element)}/>
                        </div>
                        <div className={"form-input"} key={`child-${index}-callback_data`}>
                            <label className={"form-label"}>Callback Data</label>
                            <input className={"form-control"} onChange={_ => onChangeInlineCallback(_, element)}/>
                        </div>
                    </div>
                ))}
            </article>
            <footer className={"footer"}>
                Button id: <b>{current?.id}</b>
            </footer>
        </aside>
    )
}

export default AdvancedPanel