import React, { useState } from 'react';
import CalendarGrid from './CalendarGrid';
import './CalendarExample.css';

const CalendarExample = () => {
    const [events, setEvents] = useState([
        {
            id: 1,
            start: new Date(2024, 3, 1), // 1 апреля 2024
            end: new Date(2024, 3, 5),   // 5 апреля 2024
            title: 'Конференция по React'
        },
        {
            id: 2,
            start: new Date(2024, 3, 10), // 10 апреля 2024
            end: new Date(2024, 3, 15),   // 15 апреля 2024
            title: 'Курс по TypeScript'
        }
    ]);

    const handleDateSelect = (date) => {
        console.log('Выбрана дата:', date);
    };

    const handleEventAdd = (newEvent) => {
        setEvents([...events, newEvent]);
    };

    return (
        <div className="calendar-example">
            <div className="calendar-description">
                <h2>Календарь с длительными событиями</h2>
                <div className="calendar-description-text">
                    <p>Пример использования календаря с длительными событиями:</p>
                    <ul>
                        <li>Зеленым цветом отмечены дни с событиями</li>
                        <li>Голубым цветом отмечен выбранный диапазон дат</li>
                        <li>Синим цветом отмечен выбранный день</li>
                    </ul>
                </div>
            </div>
            <div className="calendar-grid-container">
                <CalendarGrid 
                    onDateSelect={handleDateSelect} 
                    initialEvents={events}
                    onEventAdd={handleEventAdd}
                />
            </div>
            <div className="events-list">
                <h3>Список событий:</h3>
                {events.map(event => (
                    <div key={event.id} className="event-item">
                        <span className="event-title">{event.title}</span>
                        <span className="event-dates">
                            {event.start.toLocaleDateString('ru-RU')} - {event.end.toLocaleDateString('ru-RU')}
                        </span>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default CalendarExample; 