import {useEffect, useState} from "react";
import "./calendarScreen.css"
import {Left, Right} from "../components/Icon";
import {useCalendarContext} from "../contexts/calendarContext";
import { Helper } from "../constants/Helper";

const months = [
    "Январь",
    "Февраль",
    "Март",
    "Апрель",
    "Май",
    "Июнь",
    "Июль",
    "Август",
    "Сентябрь",
    "Октябрь",
    "Ноябрь",
    "Декабрь",
]
const findByDate = (array, date) => {
    return array.find(element => element.date === date);
}
const DayItem = ({day}) => (
    <td>{day}</td>
)
const Header = ({days}) => {
    const {month, year, prevMonth, nextMonth} = useCalendarContext()
    return (
        <thead>
        <tr key={"header"}>
            <td rowSpan={2} style={{width: "150px"}}>
                <div className={"button"} onClick={_ => prevMonth()}>
                    <Left/>
                    <span> Назад </span>
                </div>
            </td>
            <td colSpan={days}>
                <span>{months[month]} {year}</span>
            </td>
            <td rowSpan={2} style={{width: "150px"}}>
                <div className={"button"} onClick={_ => nextMonth()}>
                    <span> Вперед </span>
                    <Right/>
                </div>
            </td>
        </tr>
        <tr key={"days"}>
            {Array.from(Array(days).keys()).map((_, index) => (
                <DayItem day={index + 1} key={`${year}-${month}-${index}`}/>))}
        </tr>
        </thead>
    )
}
const BodyRow = ({element, days, onModal}) => {
    const {month, year} = useCalendarContext()
    const currentDate = new Date();
    element.total = 0;
    element.payed = 0;
    element.unpayed = 0;
    return (
        <tr key={`coworker-${element.id}`}>
            <td>{element.name}</td>
            {Array.from(Array(days).keys()).map((_, index) => {
                const dateStr = `${year}-${month < 9 ? "0" + (month + 1) : month + 1}-${index < 9 ? "0" + (index + 1) : index + 1}`
                const el = findByDate(element.data, dateStr)
                const newDate = new Date(year, month, index + 1)
                if (el) {
                    if (el.is_payed) {
                        element.payed += el.count;
                    } else {
                        element.unpayed += el.count;
                    }
                }
                if (currentDate > newDate) {
                    return (
                        <td key={`${element.id}-${dateStr}`} data-key={dateStr} data-id={element.id}
                            className={el ? el?.is_payed ? "is-payed" : "un-payed" : "free"} onClick={onModal}>
                            {el ? el.count : 0}
                        </td>
                    )
                } else {
                    return (<td key={`${element.id}-${dateStr}`} className={"disable"}></td>)
                }
            })}
            <td className={"disable"}>
                <span className={"is-payed"}>{element.payed}</span>&nbsp;
                <span className={"un-payed"}>{element.unpayed}</span>&nbsp;
                <span className={"total"}>{element.payed + element.unpayed}</span>
            </td>
        </tr>
    )
}
const Footer = ({data, days}) => {
    let payed = 0;
    let un_payed = 0;
    for (const element of data) {
        payed += element.payed;
        un_payed += element.unpayed;
    }
    return (
        <tfoot>
        <tr>
            <td colSpan={days + 1}>Итого</td>
            <td><span className={"is-payed"}>{payed}</span> <span className={"un-payed"}>{un_payed}</span> <span
                className={"total"}>{payed + un_payed}</span></td>
        </tr>

        </tfoot>
    )
}
const Body = ({data, days, onModal}) => {
    return (
        <tbody>
        {data.map((element, index) => (
            <BodyRow element={element} key={`coworker-${index}`} days={days} onModal={onModal}/>
        ))}
        </tbody>
    )
}

function Table({onModal}) {
    const {month, year} = useCalendarContext()
    const [days, setDays] = useState((year && month) ? (new Date(year, month + 1, 0)).getDate() : 0)
    const [info, setInfo] = useState([])
    useEffect(() => {
        if (year && month) {
            setDays((new Date(year, month + 1, 0)).getDate())
        }
        fetch(`${Helper.url}/site/calendar-month?year=${year}&month=${month + 1}`)
            .then(e => e.json())
            .then(response => {
                console.log(response)
                setInfo(response)
            })
    }, [month, year]);
    return (
        <table className={"table table-striped table-hover"}>
            <Header days={days}/>
            <Body data={info} days={days} onModal={onModal}/>
            <Footer data={info} days={days}/>
        </table>
    )
}

const Modal = ({visible, header, children, onClose, footer}) => {
    return (
        <div className={['modal', visible ? 'show' : ''].join(' ')} role={'dialog'}>
            <div className={'modal-dialog'}>
                <div className={'modal-content'}>
                    <div className={'modal-header'}>
                        <h3 className={'modal-title'}>{header}</h3>
                        <button type={'button'} className={'btn btn-default btn-close'} onClick={onClose}>&times;</button>
                    </div>
                    <div className={'modal-body'}>
                        {children}
                    </div>
                    {footer()}
                </div>
            </div>
        </div>
    )
}
const ModalFooter = ({children}) => (
    <div className={"modal-footer d-flex flex-row justify-content-between text-uppercase"}>
        {children}
    </div>
)

export default function CalendarScreen() {
    const [visible, setVisible] = useState(false)
    const [orderList, setOrderList] = useState([])
    const [orderId, setOrderId] = useState("")
    const [count, setCount] = useState(0)
    const [isPayed, setIsPayed] = useState(false)
    const [date, setDate] = useState("")
    const [coworkerId, setCoworkerId] = useState("")

    const onModal = (event) => {
        const date = new Date(event.currentTarget.getAttribute('data-key'));
        setDate(date)
        const elements = document.getElementsByClassName('modal');
        if (elements) {
            for (const element of elements) {
                element.style = "display: block";
            }
            setCoworkerId(event.currentTarget.getAttribute('data-id'))
            setVisible(true)
        }
    }
    const onClose = (event) => {
        event.currentTarget.closest('.modal').style = "";
        setVisible(false)
    }
    const onConfirm = (event) => {
        fetch(`${Helper.url}/api/order/set-hours`, {
            method: 'POST',
            headers: new Headers({
                ContentType: 'application/json',
                Authorization: 'Bearer ' + window.token
            }),
            body: JSON.stringify({
                coworker_id: coworkerId,
                date: date,
                order_id: orderId,
                count: count,
                is_payed: isPayed
            })
        })
            .then(response => response.json())
            .then(response => {
                console.log(response)
            })
            .catch(error => console.error(error))
    }
    useEffect(() => {
        fetch(`${Helper.url}/order/get-list`)
            .then(response => response.json())
            .then(response => {
                console.log(response)
                setOrderList(response ?? [])
            })
            .catch(error => console.error(error))
    }, []);
    return (
        <div id={"calendar"}>
            <Table onModal={onModal}/>
            <Modal visible={visible} header={"Почасовка"} onClose={onClose} onConfirm={onConfirm} footer={() => (
                <ModalFooter>
                    <button className={"btn btn-danger"} onClick={onClose}>Отмена</button>
                    <button className={"btn btn-success"} onClick={event => {
                        onClose(event)
                        onConfirm(event)
                    }}>Сохранить
                    </button>
                </ModalFooter>
            )}>
                <input type={"hidden"} value={date} id={"date"}/>
                <div className={"form-group mb-3"}>
                    <label htmlFor={"order"}>Заказ</label>
                    <select className={"form-control"} id={"order"} value={orderId}
                            onChange={event => setOrderId(event.currentTarget.value)}>
                        {orderList.map(element => (
                            <option value={element.id} key={`order-list-${element.id}`}>Заказ #{element.id}</option>
                        ))}
                    </select>
                </div>
                <div className={"form-group mb-3"}>
                    <label htmlFor={"count"}>Количество</label>
                    <input type={"number"} value={count} className={"form-control"} id={"count"} onChange={event => {
                        setCount(Number.parseInt(event.currentTarget.value))
                    }}/>
                </div>
                <div className={"form-check mb-3"}>
                    <input className={"form-check-input"} type={"checkbox"} id={"is-payed"} checked={isPayed} onChange={event => {
                        setIsPayed(event.currentTarget.checked)
                    }}/>
                    <label className={"form-check-label"} htmlFor={"is-payed"}>Оплачено</label>
                </div>
            </Modal>
        </div>
    )
}