

export const Helper = {
    url: 'https://build.amgcompany.ru',
    apiUrl: 'https://build.amgcompany.ru/api',
    token: window.token,
    buildFormData: (data) => {
        const formData = new FormData()
        for (const key in data) {
            formData.append(key, data[key])
        }
        return formData
    }
}
